import { Container } from "react-bootstrap";
import { PubliCus, PubliCusBox, Publifiex } from "./styled-components";

import PubliCusimg from "../../assets/img/PubliCus.png";
import Sector from "./Sector/Sector";
import PracticalProject from "./PracticalProject/PracticalProject"

const PublicCustomization = () => {
  return (
    <PubliCus>
      <Container>
        <Publifiex>
          <PubliCusBox>
            <h4>
              탄탄한 교육과정으로 공공 맞춤 빅데이터 인재 양성
              <br />
              공공 빅데이터 확산에 기여
            </h4>

            <li>공공기관 빅데이터 전문가 양성 과정(2개월) 기획</li>
            <div className="PubliCusborder">
              <h4>공공기관 특성을 반영한 빅데이터 기획역량</h4>
              <p>
                빅데이터 분석 약량 강화 과정 공공 시나리오와 공공 데이터를
                활용한
                <br />
                빅데이터 분석 시나리오, 주여 사영하는 분석기법 반영
              </p>
            </div>
          </PubliCusBox>
          <div>
            <img src={PubliCusimg} alt="" />
          </div>
        </Publifiex>
        <Sector/>
        <PracticalProject/>
      </Container>
    </PubliCus>
  );
};

export default PublicCustomization;
