import styled from "styled-components";

import { Nav, Navbar } from "react-bootstrap";

export const MeunNavbar = styled(Navbar)`
  position: static;
  transform: position 0.5s ease-in-out;
  border-bottom: 1px solid #f1f1f1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(25px);
  &.sticky {
    background-color: #fff;
    background-color: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(25px);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
  }
`;

export const Menuimg = styled(Navbar.Brand)`
  img {
    width: 84px;
  }
`;

export const MenuNever = styled(Nav)`
  padding: 10px 0px;
  font-size: 16px;
  font-weight: 700;

  a {
    margin-left: 8vh;
    color: #000;

    &:hover {
      color: #1f65ce;
    }
  }
`;
