import styled from "styled-components";

export const Contactsection = styled.section`
  margin-top: 8vh;
  h4 {
    font-size: 52px;
    font-weight: 900;
    color: #1f65ce;
  }
`;

export const Detailedinf = styled.div`
  margin-top: 5vh;
  display: flex;
  justify-content: space-between;

  .Button-Det {
    display: flex;
    align-items: start;
    justify-content: space-between;
  }
`;

export const Directions = styled.div`
  border: ${(props) =>
    props.clicked ? "2px solid #E4E4E4" : "2px solid #1F65CE"};
  box-shadow: ${(props) =>
    props.clicked ? "none" : "0px 0px 12px rgba(0, 0, 0, 0.25)"};
  transition-property: border, box-shadow;
  transition-duration: 0.2s;
  border-radius: 20px;
  padding: 4.5vh;
  width: 180px;
  background-color: #fff;
  :hover {
    cursor: ${(props) => (props.clicked ? "pointer" : "default")};
    border: ${(props) =>
      props.clicked ? "2px solid #9fbeee" : "2px solid #1F65CE"};
    transition-property: border, box-shadow;
    transition-duration: 0.2s;
    :active {
      transform: ${(props) => (props.clicked ? "scale(0.95)" : "none")};
    }
  }
  div {
    text-align: center;
    p {
      margin-top: 14px;
      margin-bottom: 0;
      font-size: 18px;
      font-weight: bold;
      color: #000;
    }
  }
`;

export const Conticon = styled.div`
  margin-top: 5vh;
  font-size: 16px;
  font-weight: 500;
  color: #000;

  .Railway {
    display: flex;
    align-items: center;
    img {
      margin-right: 5vh;
    }
  }
  .Bus {
    display: flex;
    align-items: start;
    img {
      margin-right: 5vh;
    }
    .Color-bull {
      .all {
        margin-top: 3.2vh;
      }
      .all-text {
        display: flex;
        align-items: center;
        margin-top: 1.5vh;
        p {
          margin-bottom: 0;
          margin-left: 2.5vh;
        }
        .Blue {
          width: 16px;
          height: 16px;
          background-color: #1f65ce;
          border-radius: 100px;
        }
        .Green {
          width: 16px;
          height: 16px;
          background-color: #66c056;
          border-radius: 100px;
        }
        .Red {
          width: 16px;
          height: 16px;
          background-color: #ed2222;
          border-radius: 100px;
        }
      }
    }
  }
`;

export const Inquiry = styled.div`
  h4 {
    font-size: 22px;
    font-weight: 700;
    color: #000;
    padding-top: 32px;
    padding-bottom: 12px;
    border-bottom: 6px solid #1f65ce;
    width: 15%;
  }
`;

export const Inquirytex = styled.div`
  margin-top: 12px;
  margin-bottom: 12px;
  font-size: 16px;
  font-weight: 500;
  color: #535353;
`;
