import { Button } from "react-bootstrap";
import styled from "styled-components";

export const Sitelntrsection = styled.section`
  margin-top: 12vh;
  padding-top: 9vh;
  padding-bottom: 9vh;
  background-color: #f4f4f4;
  hr {
    margin-top: 10vh;
    margin-bottom: 10vh;
  }
`;

export const SitelntrBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  .Siteln {
    img {
      width: 56px;
      height: 58px;
    }

    h4 {
      margin-top: 8vh;
      font-size: 36px;
      font-weight: bold;
      color: #1f65ce;
    }

    p {
      margin-top: 3vh;
      line-height: 1.8;
      color: #2d2d2d;
    }
  }
`;

export const OnClickButton = styled(Button)`
  margin-top: 5vh;
  width: 140px;
  padding: 16px;
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.25);
  border-radius: 12px;

  font-size: 16px;
  font-weight: bold;
  color: #515151;
`;
