import routes from "./Components/Router/Routes";
import Menu from "./Components/MenuNever/Menu";
import {
  BrowserRouter as BrowserRouter,
  Route,
  Routes,
} from "react-router-dom";

const App = () => {

  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Menu />
      <Routes>
        {routes.map((route) => {
          return (
            <Route
              key={route.path}
              path={route.path}
              element={<route.component />}
            />
          );
        })}
      </Routes>
    </BrowserRouter>
  );
};

export default App;
