import styled from "styled-components";

export const Practical = styled.section`
  margin-top: 8vh;
`;

export const PracticalBox = styled.div`
  display: flex;
  justify-content: space-between;
  img {
    margin-top: 14vh;
    width: 95px;
    height: 92px;
  }
`;

export const Prwidebox = styled.div`
  width: 306px;
  border: 2px solid #e4e4e4;
  border-radius: 12px;

  .Practibul-box {
    background-color: #5e93e1;
    border-radius: 12px 12px 0 0;
    h4 {
      text-align: center;
      font-size: 18px;
      font-weight: bold;
      padding: 16px;
      line-height: 1.6;
      color: #fff;
    }
  }

  .Prac-font {
    h4 {
      padding: 32px 0px 32px 0px;
    }
  }

  .Pract-text {
    padding: 4px;
    ul {
      li {
        font-weight: bold;
        &::before {
        }
        ul {
          padding-left: 20px;
          margin-top: 6px;
          margin-bottom: 12px;
          li {
            font-weight: normal;
            list-style-type: none;
            &::before {
              content: "-";
              padding-right: 5px;
            }
          }
        }
      }
    }
  }

  .Pract-li {
    margin-top: 18px;
  }
`;
