import { Container } from "react-bootstrap";

import { ContainerBox, Process } from "./styled-components";

import Processimg from "../../assets/img/Process.png";

const Characteristics = () => {
  return (
    <Container>
      <ContainerBox>
        <div>
          <h1>교육프로그램 특징</h1>
          <p>기업 맞춤형 데이터분석 교육</p>
        </div>
        <Process>
          <img src={Processimg} alt="" />
        </Process>
      </ContainerBox>
    </Container>
  );
};

export default Characteristics;
