import React, { useState, useEffect } from "react";
import { Container } from "react-bootstrap";
import { Loader } from "@googlemaps/js-api-loader";

import {
  Contactsection,
  Detailedinf,
  Directions,
  Inquiry,
  Inquirytex,
} from "./styled-components";

import Companyicon from "../../assets/img/company.svg";
import Callicon from "../../assets/img/call.svg";

import ConticonBox from "./Conticon/ConticonBox";
import Coninqu from "./Conticon/Call/Coninqu";
// import Call from "./Conticon/Call/Call";

const Contact = () => {
  const [clicked, setClicked] = useState(1);
  const [map, setMap] = useState(null);

  const handleClick = (e) => setClicked(e);

  useEffect(() => {
    if (clicked !== 1) return;
    const initMap = async () => {
      const loader = new Loader({
        apiKey: process.env.REACT_APP_GOOGLE_MAP_API_KEY,
        version: "weekly",
      });
      await loader.load();

      const map = new window.google.maps.Map(
        document.getElementById("map"),
        {
          center: { lat: 37.5793341, lng: 126.8900514 },
          zoom: 18,
        },
        []
      );

      setMap(map);
    };

    initMap();
  }, [clicked, setMap]);

  return (
    <Contactsection>
      <Container>
        <h4>Contact</h4>
        <Detailedinf>
          <div>
            <div className="Button-Det">
              <Directions
                clicked={clicked === 2}
                onClick={() => handleClick(1)}
              >
                <div>
                  <img src={Companyicon} alt="" />
                  <p>오시는길</p>
                </div>
              </Directions>
              <Directions
                clicked={clicked === 1}
                onClick={() => handleClick(2)}
              >
                <div>
                  <img src={Callicon} alt="" />
                  <p>문의연락</p>
                </div>
              </Directions>
            </div>
            {clicked === 1 ? <ConticonBox /> : <Coninqu />}
          </div>
          {clicked === 1 ? (
            <div>
              <div
                id="map"
                style={{
                  height: "510px",
                  width: "471px",
                  borderRadius: "20px",
                  border: "2px solid #E4E4E4",
                }}
              />
              <div>
                <Inquiry>
                  <h4>주소</h4>
                </Inquiry>
                <Inquirytex>
                  서울 특별시 마포구 월드컵북로 396
                  <br />
                  누리꿈스퀘어 비즈니스 센터, 8층 (주)아이리포
                </Inquirytex>
                <Inquiry>
                  <h4>연락처</h4>
                </Inquiry>
                <Inquirytex>smartedu@ilifo.kr / 02-6356-0182</Inquirytex>
              </div>
            </div>
          ) : (
            // <Call />
            "테스트 중입니다"
          )}
        </Detailedinf>
      </Container>
    </Contactsection>
  );
};

export default Contact;
