import { SectorBox, Sectorflex } from "./styled-components";

import Dataimg from "../../../assets/img/Dataicon.svg";
import Userimg from "../../../assets/img/Usericon.svg";
import Conceptimg from "../../../assets/img/Concepticon.svg";

const Sector = () => {
  return (
    <SectorBox>
      <Sectorflex>
        <div>
          <img src={Dataimg} alt="" />
          <div>
            <h4>공공분야 빅데이터</h4>
            <p>
              전문 기업이 직접 개발한 공공 맞춤형
              <br />
              빅데이터 과정
            </p>
          </div>
        </div>
        <div>
          <img src={Userimg} alt="" />
          <div>
            <h4>공공기관 이용자</h4>
            <p>
              공공기관 약 1240명의
              <br />
              공공 빅데이터 개념과 실무교육을 통한
              <br />
              데이터기반 일하는 방식의 변화!
            </p>
          </div>
        </div>
        <div>
          <img src={Conceptimg} alt="" />
          <div>
            <h4>공공 빅데이터 개념</h4>
            <p>
              공공 빅데이터 개념과정 5개 과목
              <br />
              시각화과정 3개, 분석활용과정 5개
              <br />
              실무 시나리오 10개 개발•교육!
            </p>
          </div>
        </div>
      </Sectorflex>
    </SectorBox>
  );
};

export default Sector;
