import { MenuNever, Menuimg, MeunNavbar } from "./styled-components";
import { Nav, Container } from "react-bootstrap";

import { useEffect, useState } from "react";

import ilifo_logo from "../../assets/img/ilifo-logo.png";

const Menu = () => {
  const [isSticky, setSticky] = useState(false);

  const checkScrollTop = () => {
    setSticky(window.scrollY >= 0);
  };

  useEffect(() => {
    window.addEventListener("scroll", checkScrollTop);
    return () => {
      window.removeEventListener("scroll", checkScrollTop);
    };
  });
  return (
    <MeunNavbar variant="light" className={isSticky ? "sticky" : ""}>
      <Container>
        <Menuimg href="#home">
          <img src={ilifo_logo} alt="" />
        </Menuimg>
        <MenuNever>
          <Nav.Link href="#home">교육서비스</Nav.Link>
          <Nav.Link href="#features">스마트에듀</Nav.Link>
          <Nav.Link href="#pricing">출판</Nav.Link>
        </MenuNever>
      </Container>
    </MeunNavbar>
  );
};

export default Menu;
