import styled from "styled-components";

export const Eduservicesbox = styled.div`
  margin-top: 80px;
  font-size: 16px;
  font-weight: 500;
  color: #535353;

  h3 {
    margin-bottom: 24px;
    font-size: 32px;
    font-weight: 700;
    color: #1f65ce;
  }

  li {
    margin-top: 30px;
    font-weight: 700;
    color: #1f65ce;
  }

  hr {
    margin-top: 35px;
  }
`;

export const Eduserbox = styled.div`
  margin-top: 32px;
  margin-bottom: 6vh;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  h4 {
    margin-top: 50px;
    font-size: 20px;
    font-weight: 700;
    color: #000;
  }
`;

export const EdusFlex = styled.div`
  align-items: stretch;
`;

export const Edarticle = styled.div`
  margin-top: 24px;
  padding: 28px;
  border-radius: 8px;
  border: 2px solid #e4e4e4;

  font-size: 16px;
  color: #535353;
  line-height: 1.6;
`;
