import { Conticon } from "../styled-components";

import Railwayicon from "../../../assets/img/Railway.svg";
import Busicon from "../../../assets/img/Bus.svg";

const ConticonBox = () => {
  return (
    <div>
      <Conticon>
        <div className="Railway">
          <img src={Railwayicon} alt="" />
          <div>
            디지털 미디어 시티역 공항철도 9번출구에 아이리포 까지
            <br /> 걸어서 13 ~ 15분
          </div>
        </div>
      </Conticon>
      <Conticon>
        <div className="Bus">
          <img src={Busicon} alt="" />
          <div className="Color-bull">
            디지털 미디어 시티역 공항철도 9번출구에 아이리포 까지
            <br /> 걸어서 13 ~ 15분
            <div className="all">
              <div className="all-text">
                <div className="Blue"></div>
                <p>171,271,470,673,710,760,771,N72</p>
              </div>
              <div className="all-text">
                <div className="Green"></div>
                <p>6715,7019,7711,7715,7730,8762</p>
              </div>
              <div className="all-text">
                <div className="Red"></div>
                <p>9711</p>
              </div>
            </div>
          </div>
        </div>
      </Conticon>
    </div>
  );
};

export default ConticonBox;
