import styled from "styled-components";

export const PubliCus = styled.section`
  margin-top: 8vh;
`;

export const Publifiex = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;

  img {
    border-radius: 12px;
  }
`;

export const PubliCusBox = styled.div`
  h4 {
    font-size: 32px;
    font-weight: 700;
    color: #1f65ce;
    line-height: 1.6;
  }

  li {
    font-size: 16px;
    font-weight: 600;
    margin-top: 7vh;
    color: #535353;
  }

  .PubliCusborder {
    margin-top: 18px;
    padding: 24px;
    border: 2px solid #e4e4e4;
    border-radius: 12px;
    width: 580px;

    h4 {
      font-size: 18px;
      font-weight: bold;
      color: #000;
    }
    p {
      font-size: 16px;
      font-weight: 500;
      color: #535353;
      margin-bottom: 0px;
    }
  }
`;
